import Countup from "react-countup";
import React from 'react';
import "./infoBox.css"
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
const useStyles = makeStyles({
    root: {
        minWidth: 275,
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    title: {
        fontSize: 14,
    },
    pos: {
        marginBottom: 12,
    },
});


export function InfoBox({ data }) {

    const classes = useStyles();
    const bull = <span className={classes.bullet}>â€¢</span>;

    return (
        <Card className={classes.root}>
            <CardContent className='confirmed'>
                <Typography className={classes.title} color="textSecondary" gutterBottom>

                    <span className='blue'>INFECTED</span>

                </Typography>

                <Typography className={classes.pos} color="textSecondary">

                    <Countup
                        start={0}
                        end={data.confirmed.value}
                        duration={2.5}
                        separator=',' />

                </Typography>
                <Typography className={classes.title} color="textSecondary" gutterBottom>
                    <span className='date'>{new Date(data.lastUpdate).toDateString()}</span>
                </Typography>
                <Typography variant="body2" component="span">
                    Numbers of the active casses of the COIVD-19
                </Typography>
            </CardContent>





            <CardContent className='recovered'>
                <Typography className={classes.title} color="textSecondary" gutterBottom>
                    <span className='green'>RECOVERED</span>
                </Typography>

                <Typography className={classes.pos} color="textSecondary">

                    <Countup
                        start={0}
                        end={data.recovered.value}

                        duration={2.5}
                        separator=',' />

                </Typography>
                <Typography className={classes.title} color="textSecondary" gutterBottom>
                    <span className='date'>{new Date(data.lastUpdate).toDateString()}</span>
                </Typography>
                <Typography variant="body2" component="span">
                    Numbers of recoveries from  the COIVD-19
                </Typography>
            </CardContent>




            <CardContent className='deaths'>
                <Typography className={classes.title} color="textSecondary" gutterBottom>
                    <span className='red'>DEATHS</span>                </Typography>

                <Typography className={classes.pos} color="textSecondary">

                    <Countup
                        start={0}
                        end={data.deaths.value}

                        duration={2.5}
                        separator=',' />

                </Typography>
                <Typography className={classes.title} color="textSecondary" gutterBottom>
                    <span className='date'>{new Date(data.lastUpdate).toDateString()}</span>
                </Typography>
                <Typography variant="body2" component="span">
                    Numbers of the deaths caused by COIVD-19
                </Typography>
            </CardContent>

        </Card>
    );
}


