import './App.css';
import React, { useEffect, useState } from "react";
import { Navbar } from "./Components/NavBar/Navbar";
import { InfoBox } from "./Components/infoBox/infoBox";
import { fetchData } from "./Components/api/index";
import { Chart } from "./Components/Chart/chart";


const App = () => {


  
const [country, setCountry]=useState("");
  let [data, setData] = useState({
    confirmed: { vlaue: 0 },
    recovered: { vlaue: 0 },
    deaths: { vlaue: 0 },
    lastUpdate: { vlaue: 0 },
  })
  useEffect(() => {
    const getData = async () => {
      let fetch = await fetchData();
      setData(fetch)
      setCountry("global")
      console.log(data);
      console.log(fetch);

    }
    getData();
  }, [])



 let handleCountryChange = async (country)=> {
   
  let fetchDataGlobal = await fetchData();
  if(country=="global"){
    setData(fetchDataGlobal)
    setCountry("global")
    
  }else{
    let countryData= await fetchData(country)
    setData(countryData);
    setCountry(country)
  }



  }






 

  return (
    <div>
      <Navbar handlCountryChange={handleCountryChange } />
      <InfoBox data={data} />

      <Chart data={data} country={country} />
    </div>
  )

}







export default App;
