

import React from 'react';
import './Navbar.css';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { alpha, makeStyles } from '@material-ui/core/styles';


/////////////////////


import { countries } from "../api/index";
import { NativeSelect, FormControl } from "@material-ui/core";
import { useEffect, useState } from "react";



////////////////////

const useStyles = makeStyles((theme) => ({

  root: {
    flexGrow: 1,
    background: 'balck'
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));



export function Navbar({ handlCountryChange }) {
  ////////////////////////

  let [fetchedCountry, setfetchedCountry] = useState([])
  useEffect(() => {
    const fetchAPI = async () => {
      setfetchedCountry(await countries());

    }
    fetchAPI();
  }, [setfetchedCountry])
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>

          {/* <Typography className={classes.title}>
          
              COVID-19 Tracker App

          </Typography> */}
          <h1 className='header'>    COVID-19 Tracker App </h1>


          <FormControl>
          <NativeSelect className='country' defaultValue="" onChange={(e) => handlCountryChange(e.target.value)}>
          {/* <select name="" id="" className='country' defaultValue="" onChange={(e) => handlCountryChange(e.target.value)}> */}


            <option value="global" className='option'>
              Global
            </option>
            {fetchedCountry.map((country, i) => <option key={i} value={country}>{country}</option>)}
          </NativeSelect>

          </FormControl>
        </Toolbar>
      </AppBar>
    </div>
  );
}
